@import '/src/styles/shared.scss';

.Form {
    display: flex;
    justify-content: center;
    align-items: end;
}

.Input {
    display: flex;

    margin-right: $elements-margin;
}

.Label {
    display: flex;
    justify-content: center;
    align-items: center;

    font-size: $small-font-size;
    line-height: 1.5;
}

.Svg {
    width: $svg-size;
    height: $svg-size;
}